/* menu btn styling */
.scroll-up-btn {
  position: fixed;
  height: 45px;
  width: 42px;
  background: $primary;
  right: 30px;
  bottom: 10px;
  text-align: center;
  line-height: 45px;
  color: #fff;
  z-index: 9999;
  font-size: 30px;
  border-radius: 6px;
  border-bottom-width: 2px;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease;

  & .show {
    bottom: 30px;
    opacity: 1;
    pointer-events: auto;
  }
}

.scroll-up-btn:hover {
  filter: brightness(90%);
}
